<template>
  <div class="categories_container">
    <el-card class="box-card">
      <el-button type="primary" class="addroles" @click="addroles"
        >添加角色</el-button
      >
      <!-- 表格 -->
      <zk-table
        ref="table"
        sum-text="sum"
        index-text="#"
        :data="goods_data"
        :columns="columns"
        :border="props.border"
        :show-header="props.showHeader"
        :show-summary="props.showSummary"
        :show-row-hover="props.showRowHover"
        :show-index="true"
        :tree-type="props.treeType"
        :is-fold="props.isFold"
        :expand-type="props.expandType"
        :selection-type="props.selectionType"
      >
        <!-- 是否有效 -->
        <template slot="sex" scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted == true"
            style="color: lightgreen"
          ></i>
          <i
            class="el-icon-error"
            v-else-if="scope.row.cat_deleted == false"
            style="color: red"
          ></i>
        </template>
        <!-- 排序 -->
        <template slot="score" scope="scope">
          <el-button
            type="primary"
            size="mini"
            plain
            v-if="scope.row.cat_level == 0"
            >一级</el-button
          >
          <el-button
            type="success"
            size="mini"
            plain
            v-else-if="scope.row.cat_level == 1"
            >二级</el-button
          >
          <el-button
            type="warning"
            size="mini"
            plain
            v-else-if="scope.row.cat_level == 2"
            >三级</el-button
          >
        </template>
        <!-- 操作 -->
        <template slot="likes" scope="scope">
          <!-- {{ scope.row }} -->
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="edit_fenlei(scope.row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="del_fenlei(scope.row.id)"
          ></el-button>
        </template>
      </zk-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.pagesize"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="params.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
      <!-- 分页 -->

      <!-- 添加分类 -->
      <el-button type="text" @click="dialogFormVisibleAdd = true"></el-button>
      <el-dialog title="添加分类" :visible.sync="dialogFormVisibleAdd">
        <el-form :model="formAdd" :rules="rules" ref="formAdd">
          <el-form-item label="分类名称:" prop="cat_name" label-width="85px">
            <el-input v-model="formAdd.cat_name" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="父及分类:">
            <el-cascader :options="options" v-model="value" :props="mode">
            </el-cascader>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisibleAdd = false">取 消</el-button>
          <el-button type="primary" @click="sure">确定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { getCategoriesApi } from "@/api/index.js";
export default {
  name: "",
  data() {
    return {
      params: {
        type: 3,
        pagenum: 1,
        pagesize: 10
      },
      props: {
        stripe: false,
        border: false,
        showHeader: true,
        showSummary: false,
        showRowHover: true,
        showIndex: false,
        treeType: true,
        isFold: true,
        expandType: false,
        selectionType: false
      },
      goods_data: [],
      columns: [
        {
          label: "分类名称",
          prop: "cat_name",
          width: "400px"
        },
        {
          label: "是否有效",
          type: "template",
          prop: "sex",
          template: "sex",
          minWidth: "50px"
        },
        {
          label: "排序",
          prop: "score",
          type: "template",
          template: "score"
        },
        {
          label: "操作",
          prop: "likes",
          minWidth: "200px",
          type: "template",
          template: "likes"
        }
      ],
      total: 0,
      // 级联选择器
      dialogFormVisibleAdd: false,
      formAdd: {
        cat_name: "",
        cat_level: 0,
        cat_pid: 0
      },
      value: [],
      mode: {
        checkStrictly: true,
        label: "cat_name",
        children: "children",
        value: "cat_id"
      },
      rules: {
        cat_name: [
          { required: true, message: "请输入分类户名", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ]
      },
      options: []
    };
  },
  created() {
    this.get_categories();
  },
  methods: {
    async get_categories() {
      const res = await getCategoriesApi(this.params);
      this.goods_data = res.data.data.result;
      this.total = res.data.data.total;
    },
    // 分页
    handleSizeChange(val) {
      this.params.pagesize = val;
      this.get_categories();
    },
    handleCurrentChange(val) {
      this.params.pagenum = val;
      this.get_categories();
    },
    // 编辑分类
    edit_fenlei(row) {},
    // 删除分类
    del_fenlei(id) {},
    // 添加角色
    async addroles() {
      this.dialogFormVisibleAdd = true;
      const res = await getCategoriesApi({ type: 2 });
      this.options = res.data.data;
    },
    // 点击确认添加分类
    sure() {
      this.$refs.formAdd.validate(async vald => {
        if (vald) {
          this.formAdd.cat_pid =
            this.value.length === 0 ? 0 : this.value[this.value.length - 1];
          this.formAdd.cat_level = this.value.length;
          await this.$http.post("categories", this.formAdd);
          this.get_categories();
          this.$message({
            message: "恭喜添加成功",
            type: "success"
          });
          this.value = [];
          this.dialogFormVisibleAdd = false;
        } else {
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.categories_container {
  .addroles {
    margin-bottom: 15px;
  }
  .el-pagination {
    margin-top: 20px;
  }
}
</style>
