<template>
  <div class="params_contioner">
    <el-card class="box-card">
      <el-alert
        title="注意：只允许为第三级分类设置相关参数！"
        type="warning"
        show-icon
      >
      </el-alert>
      <div>
        <span>选择商品分类: </span>
        <el-cascader
          v-model="value"
          :options="options"
          :props="props"
          @change="handleChange"
        ></el-cascader
        >======={{ value }}
      </div>
      <!-- tab栏 -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="动态参数" name="many">
          <el-button type="primary" :disabled="disabled">添加参数</el-button>
          <!-- 表格 -->
          <el-table :data="manyData" style="width: 100%" border>
            <el-table-column label="" width="60" type="expand">
              <template slot-scope="scoped">
                <div>
                  <!-- 动态编辑标签 -->
                  <el-tag
                    v-for="(item, index) in scoped.row.attr_vals.split(',')"
                    :key="index"
                    closable
                    :disable-transitions="false"
                    @close="handleClose(index, scoped.row)"
                    style="margin: 10px"
                  >
                    {{ item }}
                  </el-tag>
                  <el-input
                    ref="saveTagInput"
                    class="input-new-tag"
                    v-if="scoped.row.inputVisible"
                    v-model="scoped.row.inputValue"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scoped.row)"
                    @blur="handleInputConfirm(scoped.row)"
                  >
                  </el-input>
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(scoped.row)"
                    >+ New Tag</el-button
                  >
                </div>
              </template>
            </el-table-column>
            <el-table-column
              label="#"
              width="60"
              type="index"
              sortable
            ></el-table-column>
            <el-table-column label="参数名称" prop="attr_name">
            </el-table-column>
            <el-table-column label="操作">
              <template>
                <el-button type="primary" icon="el-icon-edit" size="mini"
                  >编辑</el-button
                >
                <el-button type="danger" icon="el-icon-delete" size="mini"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name="only">
          <el-button type="primary" :disabled="disabled">添加属性</el-button>
          <!-- 表格 -->
          <el-table :data="onlyData" style="width: 100%" border>
            <el-table-column label="" width="60" type="expand">
              <template slot-scope="scoped">
                <div>
                  <!-- 动态编辑标签 -->
                  <el-tag
                    v-for="(item, index) in scoped.row.attr_vals.split(',')"
                    :key="index"
                    closable
                    :disable-transitions="false"
                    @close="handleClose(index, scoped.row)"
                    style="margin: 10px"
                  >
                    {{ item }}
                  </el-tag>
                  <el-input
                    ref="saveTagInput"
                    class="input-new-tag"
                    v-if="scoped.row.inputVisible"
                    v-model="scoped.row.inputValue"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scoped.row)"
                    @blur="handleInputConfirm(scoped.row)"
                  >
                  </el-input>
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(scoped.row)"
                    >+ New Tag</el-button
                  >
                </div>
              </template>
            </el-table-column>
            <el-table-column
              label="#"
              width="60"
              type="index"
              sortable
            ></el-table-column>
            <el-table-column label="属性名称" prop="attr_name">
            </el-table-column>
            <el-table-column label="操作">
              <template>
                <el-button type="primary" icon="el-icon-edit" size="mini"
                  >编辑</el-button
                >
                <el-button type="danger" icon="el-icon-delete" size="mini"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import lodash from 'lodash'

export default {
  data () {
    return {
      value: [],
      options: [],
      activeName: 'many',
      manyData: [],
      onlyData: [],
      props: {
        label: 'cat_name',
        children: 'children',
        value: 'cat_id'
      },
      disabled: true,
      objonly: {
        attr_name: '',
        attr_sel: '',
        attr_vals: '',
        attrId: ''
      }
    }
  },
  created () {
    this.getfenleidata()
  },
  methods: {
    // 级联选择器
    handleChange (value) {
      if (value.length === 3) {
        this.disabled = false
        this.getmonydata()
        this.getonlydata()
      }
    },
    // 获取商品分类
    async getfenleidata () {
      const {
        data: { data }
      } = await this.$http.get('categories', { params: { type: 3 } })
      this.options = data
    },
    // 获取动态参数数据
    async getmonydata () {
      const res = await this.$http.get(`categories/${this.catid}/attributes`, {
        params: {
          sel: 'many'
        }
      })
      res.data.data.forEach(item => {
        item.attr_vals = lodash.compact(item.attr_vals.split(',')).join(',')
        item.inputVisible = false
        item.inputValue = ''
      })
      this.manyData = res.data.data
    },
    // 获取静态属性数据
    async getonlydata () {
      const res = await this.$http.get(`categories/${this.catid}/attributes`, {
        params: {
          sel: 'only'
        }
      })
      res.data.data.forEach(item => {
        item.attr_vals = lodash.compact(item.attr_vals.split(',')).join(',')
        item.inputVisible = false
        item.inputValue = ''
      })
      this.onlyData = res.data.data
    },
    // tab栏
    handleClick (tab, event) {
      if (tab.name === 'many') {
        this.activeName = 'many'
      } else if (tab.name === 'only') {
        this.activeName = 'only'
      }
    },
    // 删除参数
    async handleClose (index, rows, ele) {
      var arr = rows.attr_vals.split(',')
      arr.splice(index, 1)
      var str = arr.join(',')
      var obj = {
        attr_name: rows.attr_name,
        attr_sel: this.activeName,
        attr_vals: str,
        attrId: rows.attr_id
      }
      await this.$http.put(
        `categories/${this.catid}/attributes/${rows.attr_id}`,
        obj
      )
      rows.attr_vals = str
    },
    showInput (rows) {
      rows.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 添加静态属性
    async handleInputConfirm (rows) {
      this.objonly.attr_sel = this.activeName
      var arr = rows.attr_vals.split(',')
      this.objonly.attr_vals = [...arr, rows.inputValue].join(',')
      this.objonly.attr_name = rows.attr_name
      this.objonly.attrId = rows.attr_id
      await this.$http.put(
        `categories/${this.catid}/attributes/${rows.attr_id}`,
        this.objonly
      )
      arr.push(rows.inputValue)
      rows.attr_vals = arr.join(',')
      rows.inputVisible = false
      rows.inputValue = ''
    }
  },
  computed: {
    catid () {
      return this.value[this.value.length - 1]
    }
  }
}
</script>

<style lang="less" scoped>
.params_contioner {
  height: 100%;
  .box-card {
    height: 100%;
    .el-alert {
      margin-bottom: 20px;
    }
    .el-button {
      margin-bottom: 20px;
    }
    .el-input {
      width: 10%;
    }
  }
}
</style>
