<template>
  <div>
    <el-card class="box-card">
      <!-- 搜索框 -->
      <el-input
        placeholder="请输入内容"
        v-model="params.query"
        class="input-with-select"
      >
        <el-button slot="append" icon="el-icon-search"></el-button>
      </el-input>
      <el-button type="primary" @click="addusers"
        >{{ params.query }}添加商品</el-button
      >
      <!-- 搜索框 -->
      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column
          label="#"
          width="60"
          type="index"
          sortable
        ></el-table-column>
        <el-table-column label="商品名称" width="630" prop="goods_name">
        </el-table-column>
        <el-table-column label="商品价格(元)" width="180" prop="goods_price">
        </el-table-column>
        <el-table-column label="商品重量" width="180" prop="goods_weight">
        </el-table-column>
        <el-table-column label="创建时间" width="180">
          <template slot-scope="socpe">
            <div>
              {{ socpe.row.upd_time | format() }}
            </div>
          </template>
        </el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scoped">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              :ss="scoped.row"
            ></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.pagesize"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="params.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
      <!-- 分页 -->
    </el-card>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      params: {
        query: "",
        pagenum: 1,
        pagesize: 4
      },
      tableData: [],
      total: 1,
      time: new Date()
    };
  },
  created() {
    this.getgoodsdata();
  },
  methods: {
    addusers() {
      this.$router.push("/goods/add");
    },
    // 获取商品数据
    async getgoodsdata() {
      const res = await this.$http.get("goods", { params: this.params });
      console.log(res.data.data, 1111111111);
      this.tableData = res.data.data.goods;
      this.total = res.data.data.total;
    },
    // 分页
    handleSizeChange(val) {
      this.params.pagesize = val;
      this.getgoodsdata();
    },
    handleCurrentChange(val) {
      this.params.pagenum = val;
      this.getgoodsdata();
    }
  }
};
</script>
<style lang="less" scoped>
.el-input {
  width: 40%;
  margin-right: 20px;
}
.el-table {
  margin-top: 20px;
}
.el-pagination {
  margin-top: 20px;
}
</style>
